:root {
  --dark-accent1: #61892F;
  --dark-accent2: #86C232;
  --dark-accent3: #222629;
  --dark-accent4: #474B4F;
  --dark-accent5: #6B6E70;
  --dark-accent6: #16191b;
  --light-accent1: #5680E9;
  --light-accent2: #84CEEB;
  --light-accent3: #5AB9EA;
  --light-accent4: #C1C8E4;
  --light-accent5: #8860D0;
  --lv2-accent1: #EAE7DC;
  --lv2-accent2: #D8C3A5;
  --lv2-accent3: #8E8D8A;
  --lv2-accent4: #E98074;
  --lv2-accent5: #E85A4F;
  --vaporwave-accent1: #9239F6;
  --vaporwave-accent2: #903495;
  --vaporwave-accent3: #6F3460;
  --vaporwave-accent4: #4A354F;
  --vaporwave-accent5: #D20076;
  --vaporwave-accent6: #FF0076;
  --vaporwave-accent7: #FF4373;
  --vaporwave-accent8: #FF6B58;
  --vaporwave-accent9: #F8B660;
}

.App {
  text-align: center;
  font-family: Lato, sans-serif;
}

.section {
  padding: 7vmin 5vw 0;
  //height: calc(100vh - 9vmin);
  transition: all 1s cubic-bezier(0.25, 0, 0.41, 1);
  display: flex;
  flex-direction: column;
  h1 {
    flex-shrink: 0;
    text-align: left;
    font-size: 7vmin;
    //position: absolute;
    //top: 3vmin;
    //margin-top: 3vmin;
    transition: 1s ease-in-out;

    margin-block-end: 0;
    margin-block-start: 0;
  }
  .section-content {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0;
    padding: 5vmin 0;
    align-items: center;
    //margin-top: 20vmin;
    //height: calc(100% - 20vmin);
    //padding-top: 3vmin;
    //display: table-cell;
    //vertical-align: middle;
  }
}

.App[theme="dark"] {
  .section {
    background: var(--dark-accent3);
    &:last-child {
      background: var(--dark-accent6);
    }
    h1 {
      color: white;
      &:first-letter {
        color: var(--dark-accent2);
      }
    }
  }
}

.App[theme="light"] {
  .section {
    background: var(--lv2-accent1);
    &:last-child {
      background: var(--lv2-accent2);
    }
    h1 {
      color: black;
      &:first-letter {
        color: var(--lv2-accent5);
      }
    }
  }
}

.App[theme="vaporwave"] {
  background: linear-gradient(var(--vaporwave-accent1) 0%, var(--vaporwave-accent2) 100%);
  .section {
    background: transparent;
    h1 {
      color: var(--vaporwave-accent8);
      &:first-letter {
        color: var(--vaporwave-accent7);
      }
    }
  }
}